  overflow: hidden;
}
.blog-preview-content p.title {
  color: #262626;
  font-size: 18px;
  line-height: 22px;
  margin-bottom: 6px;
  font-weight: 500;
}
.blog-preview-content .blog-preview-bottom {
  position: absolute;
  bottom: 15px;
  width: calc(100% - 30px);
  height: 20px;
}
.wrapper-blog {
  margin-bottom: 15px;
}
.wrapper-blog .title {
  margin: 0 0 10px;
  font-size: 22px;
  text-transform: inherit;
}
.wrapper-blog h2 {
  font-size: 18px !important;
}
.wrapper-blog .article-date {
  margin-bottom: 15px;
}
