.modal-content {
  border-radius: 0;

  border: 1px solid rgba(0,0,0,.1);
  .box-shadow(0 3px 9px rgba(0,0,0,.3));
  .modal-header {
    padding: 15px;
    text-align: center;

    .close {
      font-size: 32px;
      margin-top: -8px;
      font-weight: 100;
    }

    .modal-icon {
      font-size: 64px;
      color: #e2e3e3;
    }

    .modal-title {
      font-size: 26px;
    }
  }

  .modal-body {
    background: #f8fafb;
  }
}

.modal-header {
  border-bottom: 1px solid @border-color-base;
}

.modal-footer {
  border-top: 1px solid @border-color-base;
}

.modal-backdrop {
  &.in { .opacity(0.3); }
}

.modal-login {
  width: 350px;

  .modal-body {
    background: #fff;
    padding: 30px;
  }
}

#locationModal,
#themeLocationModal {
  .modal-body {
    padding-bottom: 0;
    .city-select {
      padding-left: 5px;
    }

    .location-list {
      padding: 0;
      list-style: none;
      .clearfix();
      li {
        padding-bottom: 5px;
      }
    }

    .location-action {
      padding: 15px;
      background: @modal-content-bg;
      border-top: 1px solid #e5e5e5;
    }
    .js-city__link {
      color: @text-color;
      //font-size: 14px;
      cursor: pointer;
      transition: all 0.3s;
      &:hover {
        color: @green;
      }
    }
  }
  .col-xs-4 {
    width: 33.33333333%;
  }
}

.bootbox-confirm {
  .modal-header {
    padding: 10px 15px;
    border-bottom: none;
  }
  .modal-body {
    padding: 10px 15px;
    background: #fff;
  }

  .modal-footer {
    padding: 10px 15px;
    border-top: none;
  }
}