******************/

input {outline: none;}
input:-webkit-autofill {
  -webkit-box-shadow: inset 0 0 0 50px #fff !important; /* Цвет фона */
  -webkit-text-fill-color: #000 !important; /* цвет текста */
}

.form-control {
  height: 34px;
  padding: 6px 12px;
  color: #555555;
  border: 1px solid #cbcbcb;
  border-radius: 2px;
  box-shadow: none;
  transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
}


.register-form,
.modal-login {
  .form-group {
    position: relative;

    .help-block-errors {
      display: none;
      color: @red;
      font-size: 12px;
    }

    &.has-error {
      .form-control {
        border-color: @red;
        //color: @red !important;
      }

      .help-block-errors {
        display: block;
      }
    }
  }
}

.form-group {

  .help-block-errors {
    display: none;
    color: @red;
    font-size: 12px;
  }

  &.has-error,
  .has-error{

    .form-control {
      border-color: @red;
    }

    .help-block-errors {
      display: block;
    }
  }

}

.modal-login {
  .form-group {
    margin-bottom: 2em;

    .form-control {
      padding: 0;
      border-color: #e0e0e0;
      border-radius: 0;
      border-top-width: 0;
      border-right-width: 0;
      border-left-width: 0;
      //border: 1px solid #ccc;
      box-shadow: none;
      background-color: transparent;
      color: #000;
      &:focus {
        border-color: @green;
      }

      + span {
        position: absolute;
        top: 0;
        right: 0;
        padding: 6px 0 5px 25px;
        background: #fff;
        /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffffff+0,ffffff+100&0+0,1+20 */
        background: -moz-linear-gradient(left,  rgba(255,255,255,0) 0%, rgba(255,255,255,1) 20%, rgba(255,255,255,1) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(left,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 20%,rgba(255,255,255,1) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to right,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 20%,rgba(255,255,255,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=1 ); /* IE6-9 */

        a {
          font-size: (@font-size-base - 2);
        }
      }
    }
  }
}

.input-group {
  .form-control {

  }
  .input-group-btn {
    .bootstrap-select.btn-group:not(.input-group-btn) {
      margin-left: -1px;
    }

    .bootstrap-select:not([class*="col-"]):not([class*="form-control"]):not(.input-group-btn) {
      width: auto;
    }
  }
}

.nowrap-table-data {
  min-width: 90px; // Отменяем перенос
}


.input-group-spacer {
  position: relative; // For dropdowns
  display: table;

}