.btn-green {
  // @color; @background; @border
  .button-variant(#fff; #1ab394; #1ab394);

  &.btn-outline {
    color: #1ab394;

    &:hover {
      color: #fff;
    }
  }
}

.btn-outline {
  color: inherit;
  background-color: transparent;
  transition: all .5s;
}

.btn-table {
  .button-variant(@btn-default-color; transparent; transparent);
  //color: @btn-default-color;
  font-size: 14px;
  margin-top: -5px;
  border-radius: 50%;
}

.btn-green.btn-outline {
  &:focus,
  &.focus {
    color: #fff;
    background-color: #179d82;
    border-color: #127e68;
  }
}


.btn {
  border-radius: 2px;
}

.btn-group .btn + .btn,
.btn-group .btn + .btn-group,
.btn-group .btn-group + .btn,
.btn-group .btn-group + .btn-group {
  border-left-width: 0;
}

.input-group-btn {
  .btn {
    height: @input-height-base;
  }
}

