.navbar {
  min-height: 60px;
}

.navbar-default {
  background-color: #fff;
  border-color: @border-color-base;

  &.nav-full {
    border-radius: 0;
    border-right-width: 0;
    border-left-width: 0;
  }

  .navbar-nav {

    > li {
      > a {
        padding-top: 20px;
        padding-bottom: 20px;
        color: #2b2b2b;
        font-size: (@font-size-base + 2);
      }

      &.open > a {
        &,
        &:hover,
        &:focus {
          background-color: transparent;
        }
      }
    }

    .open .dropdown-menu {
      > li > a:hover,
      > li > a:focus {
        background-color: #1ab394;
        color: #fff;
      }
    }
  }

  .navbar-text {
    color: #2b2b2b;
    margin-top: 19px;
    margin-bottom: 19px;
    float: left;
    margin-left: 10px;
    margin-right: 10px;
  }
}

.navbar-static-top {
  border-radius: 0;
}

.navbar-nav {
  > li {
    > .dropdown-menu {
      .drop-first {
        height: 8px;
        background: #fff;
        margin-top: -8px;
      }
    }
  }
}
