@background-checkbox:                #fff;
@checkbox-height:                    20px;
@checkbox-width:                     20px;

/*******************************
           Checkbox
*******************************/


/*--------------
    Content
---------------*/

.checkbox {
  position: relative;
  display: inline-block;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  outline: none;
  vertical-align: baseline;
  font-style: normal;
  min-height: @checkbox-height;
  font-size: @font-size-base;
  line-height: @checkbox-height;
  min-width: @checkbox-width;

  /* HTML Checkbox */
  input[type="checkbox"],
  input[type="radio"] {
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0 !important;
    outline: none;
    z-index: 3;
    width: @checkbox-width;
    height: @checkbox-height;
  }

  /*--------------
        Box
  ---------------*/

  .box,
  label {
    cursor: pointer;
    position: relative;
    display: block;
    padding-left: 1.85714em;
    outline: none;
    font-size: 1em;
    font-weight: normal;
  }

  .box:before,
  label:before {
    position: absolute;
    top: 0;
    left: 0;
    width: @checkbox-width;
    height: @checkbox-height;
    content: '';
    background: @background-checkbox;
    border-radius: 2px;
    transition: border 0.1s ease, opacity 0.1s ease, transform 0.1s ease, box-shadow 0.1s ease;
    border: 1px solid @input-border;
  }

  /*--------------
      Checkmark
  ---------------*/

  .box:after,
  label:after {
    position: absolute;
    top: 0;
    left: 0;
    width: @checkbox-width;
    height: @checkbox-height;
    text-align: center;
    opacity: 0;
    transition: border 0.1s ease, opacity 0.1s ease, transform 0.1s ease, box-shadow 0.1s ease;
  }

  &.small {
    .box,
    label {
      padding-left: 26px;
      font-size: 13px;
    }

    .box:after,
    label:after {
      top: -2px;
      left: 0;
    }

    input:checked ~ .box:after,
    input:checked ~ label:after {
      font-size: 13px;
    }

    input[type="checkbox"],
    input[type="radio"],
    .box:after,
    label:after,
    .box:before,
    label:before {
      width: (@checkbox-width - 4);
      height: (@checkbox-height - 4);
    }
  }
}


/*--------------
      Label
---------------*/


/* Inside */
.checkbox label,
.checkbox + label {
  padding-left: 30px;
  //color: rgba(0, 0, 0, 0.87);
  -webkit-transition: color 0.1s ease;
  transition: color 0.1s ease;
}

/* Outside */
.checkbox + label {
  vertical-align: middle;
}


/*******************************
           States
*******************************/


/*--------------
      Hover
---------------*/

.checkbox .box:hover::before,
.checkbox label:hover::before {
  background: @background-checkbox;
  border-color: rgba(34, 36, 38, 0.35);
}
.checkbox label:hover,
.checkbox + label:hover {
  color: rgba(0, 0, 0, 0.8);
}

/*--------------
      Down
---------------*/

.checkbox .box:active::before,
.checkbox label:active::before {
  background: #F9FAFB;
  border-color: rgba(34, 36, 38, 0.35);
}
.checkbox .box:active::after,
.checkbox label:active::after {
  color: rgba(0, 0, 0, 0.95);
}
.checkbox input:active ~ label {
  color: rgba(0, 0, 0, 0.95);
}

/*--------------
     Focus
---------------*/

.checkbox input:focus ~ .box:before,
.checkbox input:focus ~ label:before {
  background: @background-checkbox;
  border-color: #96C8DA;
}
.checkbox input:focus ~ .box:after,
.checkbox input:focus ~ label:after {
  color: rgba(0, 0, 0, 0.95);
}
.checkbox input:focus ~ label {
  color: rgba(0, 0, 0, 0.95);
}

/*--------------
     Active
---------------*/

.checkbox input:checked ~ .box:before,
.checkbox input:checked ~ label:before {
  background: @background-checkbox;
  border-color: rgba(34, 36, 38, 0.35);
}
.checkbox input:checked ~ .box:after,
.checkbox input:checked ~ label:after {
  opacity: 1;
  color: #2b2b2b;
  font-size: 17px;
}

/*--------------
  Indeterminate
---------------*/

.checkbox input:indeterminate ~ .box:before,
.checkbox input:indeterminate ~ label:before {
  background: @background-checkbox;
  border-color: rgba(34, 36, 38, 0.35);
}
.checkbox input:indeterminate ~ .box:after,
.checkbox input:indeterminate ~ label:after {
  opacity: 1;
  color: rgba(0, 0, 0, 0.95);
}

/*--------------
  Active Focus
---------------*/

.checkbox input:indeterminate:focus ~ .box:before,
.checkbox input:indeterminate:focus ~ label:before,
.checkbox input:checked:focus ~ .box:before,
.checkbox input:checked:focus ~ label:before {
  background: #FFFFFF;
  border-color: #96C8DA;
}
.checkbox input:indeterminate:focus ~ .box:after,
.checkbox input:indeterminate:focus ~ label:after,
.checkbox input:checked:focus ~ .box:after,
.checkbox input:checked:focus ~ label:after {
  color: rgba(0, 0, 0, 0.95);
}

/*--------------
    Read-Only
---------------*/

.read-only.checkbox,
.read-only.checkbox label {
  cursor: default;
}

/*--------------
     Disabled
---------------*/

.disabled.checkbox .box:after,
.disabled.checkbox label,
.checkbox input[disabled] ~ .box:after,
.checkbox input[disabled] ~ label {
  cursor: default;
  opacity: 0.5;
  color: #000000;
}

/*--------------
     Hidden
---------------*/


/* Initialized checkbox moves input below element
 to prevent manually triggering */
.checkbox input.hidden {
  z-index: -1;
}

/* Selectable Label */
.checkbox input.hidden + label {
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}


/*******************************
             Types
*******************************/


/*--------------
     Radio
---------------*/

.radio.checkbox {
  min-height: 22px;
}

.radio.checkbox .box,
.radio.checkbox label {
  padding-left: 30px;
}

/* Box */
.radio.checkbox .box:before,
.radio.checkbox label:before {
  content: '';
  -webkit-transform: none;
  -ms-transform: none;
  transform: none;
  width: @checkbox-width;
  height: @checkbox-height;
  border-radius: 50%;
  top: 0;
  left: 0;
}


/* Radio Checkbox */
.radio.checkbox .box:after,
.radio.checkbox label:after {
  top: 0;
  left: 0;
  width: @checkbox-width;
  height: @checkbox-height;
  line-height: 20px;
  border-radius: 50%;
  -webkit-transform: scale(0.46666667);
  -ms-transform: scale(0.46666667);
  transform: scale(0.46666667);
  background-color: #2b2b2b;
  border: none;
  content: '' !important;
}

/* Focus */
.radio.checkbox input:focus ~ .box:before,
.radio.checkbox input:focus ~ label:before {
  background-color: @background-checkbox;
}
.radio.checkbox input:focus ~ .box:after,
.radio.checkbox input:focus ~ label:after {
  background-color: #2b2b2b;
}

/* Indeterminate */
.radio.checkbox input:indeterminate ~ .box:after,
.radio.checkbox input:indeterminate ~ label:after {
  opacity: 0;
}

/* Active */
.radio.checkbox input:checked ~ .box:before,
.radio.checkbox input:checked ~ label:before {
  background-color: @background-checkbox;
}
.radio.checkbox input:checked ~ .box:after,
.radio.checkbox input:checked ~ label:after {
  background-color: #2b2b2b;
}

/* Active Focus */
.radio.checkbox input:focus:checked ~ .box:before,
.radio.checkbox input:focus:checked ~ label:before {
  background-color: @background-checkbox;
}
.radio.checkbox input:focus:checked ~ .box:after,
.radio.checkbox input:focus:checked ~ label:after {
  background-color: #2b2b2b;
}

/*--------------
     Slider
---------------*/

.slider.checkbox {
  min-height: 1.25rem;
}

/* Input */
.slider.checkbox input {
  width: 3.5rem;
  height: 1.25rem;
}

/* Label */
.slider.checkbox .box,
.slider.checkbox label {
  padding-left: 4.5rem;
  line-height: 1rem;
  color: rgba(0, 0, 0, 0.4);
}

/* Line */
.slider.checkbox .box:before,
.slider.checkbox label:before {
  display: block;
  position: absolute;
  content: '';
  border: none !important;
  left: 0em;
  z-index: 1;
  top: 0.4rem;
  background-color: rgba(0, 0, 0, 0.05);
  width: 3.5rem;
  height: 0.21428571rem;
  -webkit-transform: none;
  -ms-transform: none;
  transform: none;
  border-radius: 500rem;
  -webkit-transition: background 0.3s ease;
  transition: background 0.3s ease;
}

/* Handle */
.slider.checkbox .box:after,
.slider.checkbox label:after {
  background: #FFFFFF -webkit-linear-gradient(transparent, rgba(0, 0, 0, 0.05));
  background: #FFFFFF linear-gradient(transparent, rgba(0, 0, 0, 0.05));
  position: absolute;
  content: '' !important;
  opacity: 1;
  z-index: 2;
  border: none;
  box-shadow: 0px 1px 2px 0 rgba(34, 36, 38, 0.15), 0px 0px 0px 1px rgba(34, 36, 38, 0.15) inset;
  width: 1.5rem;
  height: 1.5rem;
  top: -0.25rem;
  left: 0em;
  -webkit-transform: none;
  -ms-transform: none;
  transform: none;
  border-radius: 500rem;
  -webkit-transition: left 0.3s ease;
  transition: left 0.3s ease;
}

/* Focus */
.slider.checkbox input:focus ~ .box:before,
.slider.checkbox input:focus ~ label:before {
  background-color: rgba(0, 0, 0, 0.15);
  border: none;
}

/* Hover */
.slider.checkbox .box:hover,
.slider.checkbox label:hover {
  color: rgba(0, 0, 0, 0.8);
}
.slider.checkbox .box:hover::before,
.slider.checkbox label:hover::before {
  background: rgba(0, 0, 0, 0.15);
}

/* Active */
.slider.checkbox input:checked ~ .box,
.slider.checkbox input:checked ~ label {
  color: rgba(0, 0, 0, 0.95) !important;
}
.slider.checkbox input:checked ~ .box:before,
.slider.checkbox input:checked ~ label:before {
  background-color: #545454 !important;
}
.slider.checkbox input:checked ~ .box:after,
.slider.checkbox input:checked ~ label:after {
  left: 2rem;
}

/* Active Focus */
.slider.checkbox input:focus:checked ~ .box,
.slider.checkbox input:focus:checked ~ label {
  color: rgba(0, 0, 0, 0.95) !important;
}
.slider.checkbox input:focus:checked ~ .box:before,
.slider.checkbox input:focus:checked ~ label:before {
  background-color: #000000 !important;
}

/*--------------
     Toggle
---------------*/

.toggle.checkbox {
  min-height: 1.5rem;
}

/* Input */
.toggle.checkbox input {
  width: 3.5rem;
  height: 1.5rem;
}

/* Label */
.toggle.checkbox .box,
.toggle.checkbox label {
  min-height: 1.5rem;
  padding-left: 4.5rem;
  color: rgba(0, 0, 0, 0.87);
}
.toggle.checkbox label {
  padding-top: 0.15em;
}

/* Switch */
.toggle.checkbox .box:before,
.toggle.checkbox label:before {
  display: block;
  position: absolute;
  content: '';
  z-index: 1;
  -webkit-transform: none;
  -ms-transform: none;
  transform: none;
  border: none;
  top: 0rem;
  background: rgba(0, 0, 0, 0.05);
  width: 3.5rem;
  height: 1.5rem;
  border-radius: 500rem;
}

/* Handle */
.toggle.checkbox .box:after,
.toggle.checkbox label:after {
  background: #FFFFFF -webkit-linear-gradient(transparent, rgba(0, 0, 0, 0.05));
  background: #FFFFFF linear-gradient(transparent, rgba(0, 0, 0, 0.05));
  position: absolute;
  content: '' !important;
  opacity: 1;
  z-index: 2;
  border: none;
  box-shadow: 0px 1px 2px 0 rgba(34, 36, 38, 0.15), 0px 0px 0px 1px rgba(34, 36, 38, 0.15) inset;
  width: 1.5rem;
  height: 1.5rem;
  top: 0rem;
  left: 0em;
  border-radius: 500rem;
  -webkit-transition: background 0.3s ease, left 0.3s ease;
  transition: background 0.3s ease, left 0.3s ease;
}
.toggle.checkbox input ~ .box:after,
.toggle.checkbox input ~ label:after {
  left: -0.05rem;
}

/* Focus */
.toggle.checkbox input:focus ~ .box:before,
.toggle.checkbox input:focus ~ label:before {
  background-color: rgba(0, 0, 0, 0.15);
  border: none;
}

/* Hover */
.toggle.checkbox .box:hover::before,
.toggle.checkbox label:hover::before {
  background-color: rgba(0, 0, 0, 0.15);
  border: none;
}

/* Active */
.toggle.checkbox input:checked ~ .box,
.toggle.checkbox input:checked ~ label {
  color: rgba(0, 0, 0, 0.95) !important;
}
.toggle.checkbox input:checked ~ .box:before,
.toggle.checkbox input:checked ~ label:before {
  background-color: #2185D0 !important;
}
.toggle.checkbox input:checked ~ .box:after,
.toggle.checkbox input:checked ~ label:after {
  left: 2.15rem;
}

/* Active Focus */
.toggle.checkbox input:focus:checked ~ .box,
.toggle.checkbox input:focus:checked ~ label {
  color: rgba(0, 0, 0, 0.95) !important;
}
.toggle.checkbox input:focus:checked ~ .box:before,
.toggle.checkbox input:focus:checked ~ label:before {
  background-color: #0d71bb !important;
}


/*******************************
            Variations
*******************************/


/*--------------
     Fitted
---------------*/

.fitted.checkbox .box,
.fitted.checkbox label {
  padding-left: 0em !important;
}
.fitted.toggle.checkbox,
.fitted.toggle.checkbox {
  width: 3.5rem;
}
.fitted.slider.checkbox,
.fitted.slider.checkbox {
  width: 3.5rem;
}


/*******************************
         Theme Overrides
*******************************/

@font-face {
  font-family: 'Checkbox';
  src: url(data:application/x-font-ttf;charset=utf-8;base64,) format('truetype');
}

/* Checkmark */
.checkbox label:after,
.checkbox .box:after {
  font-family: 'Checkbox';
}

/* Checked */
.checkbox input:checked ~ .box:after,
.checkbox input:checked ~ label:after {
  content: '\e800';
}

/* Indeterminate */
.checkbox input:indeterminate ~ .box:after,
.checkbox input:indeterminate ~ label:after {
  font-size: 12px;
  content: '\e801';
}
