@import "component/alert";
@import "component/ajax";
@import "component/filter";
@import "component/form";
@import "component/navbar";
@import "component/tabs";
@import "component/btn";
@import "component/dropdowns";
@import "component/popovers";
@import "component/bootstrap-select";
@import "component/modal";
@import "component/divider";
@import "component/checkbox";
@import "component/pagination";

html{
  height: 100%;
}

body {
  min-height: 100%;
  min-width: @screen-tablet;
  vertical-align: top;
  font-family: "Museo","Helvetica Neue","Helvetica","Arial",sans-serif;
  font-weight: 300;
  color: @text-color-base;
}

.container {
  min-width: @screen-tablet;
}

.content {
  //padding-left: @grid-gutter-width;
  padding-bottom: 15px;
  .breadcrumb {
    background-color: #fff;
    padding: 0;
    margin-bottom: 15px;
    font-size: 12px;
    > li {
      strong {
        font-weight: 500;
      }
      a {
        color: inherit;
      }
    }
  }
}

.nav > .nav-auth {
  a {
    display: inline-block;
    padding-right: 10px;
    padding-left: 10px;
  }
}

.navbar-static-top {
  .navbar-text {
    font-size: (@font-size-base + 2);
  }
}

.header {
  height: 100px;
}

.logo {
  height: 55px;
  width: 240px;
  margin-top: 8px;

  a {
    display: block;
    height: 55px;

    img {
      height: 100%;
      width: 100%;
    }
  }
}

.header-slogan {
  padding-top: 29px;
  padding-left: 30px;
  font-size: (@font-size-base + 2);
}

.header-button-add {
  padding-top: 20px;
}

a:hover,
a:focus {
  color: #18a689;
  text-decoration: none;
}

a.link {
  font-weight: 500;
  color: inherit;
  transition: border-color .15s;
  border-bottom: 1px solid hsla(0,0%,7%,.15) !important;
  &:hover,
  &:focus {
    border-bottom-color: hsla(0,0%,7%,.3) !important;
  }
}

.ajax-error {
  display: none;
}

#modalAuth{
  .ajax-error {
    color: red;
    margin: 5px 0 15px 0;
    line-height: 17px;
    font-size: 13px;
    font-weight: 500;
  }
}

.modal-login {
  .form {
    padding-top: 15px;
  }

  .divider {
    margin: 20px 0 10px;
  }
}

.modal-text-social,
.register-text-social {
  font-size: 13px;
  color: #919191;
}

.modal-register {
  padding-top: 20px;
  a {
    font-style: italic;
    font-size: 15px;
  }
}

/***************************
   Модал соц. ссылки
***************************/

ul.social-network {
  list-style: none;
  display: block;
  margin-left:0 !important;
  padding: 15px 0 0;
}

.social-circle {
  li {
    display: inline;
    margin: 0 5px;
    opacity: 0.85;
    &:hover {
      opacity: 1;
    }
    a {
      display:inline-block;
      position:relative;
      margin:0 auto;
      border-radius:50%;
      text-align:center;
      width: 42px;
      height: 42px;
      font-size:20px;
      border: 2px solid transparent;
      //background: @green;

      &.icoFacebook {
        border-color: #3B5998;
        color: #3B5998;
      }
      &.icoTwitter {
        border-color: #33ccff;
        color: #33ccff;
      }
      &.icoGoogle {
        border-color: #BD3518;
        color: #BD3518;
      }
      &.icoVk {
        border-color: #527498;
        color: #527498;
      }
      &.icoOk {
        border-color: #ee8208;
        color: #ee8208;
      }
      i {
        margin: 0;
        line-height: 38px;
        text-align: center;
        //color: #fff;
        transition: all 0.8s;
      }

      &:hover i {
        transform: rotate(360deg);
        transition: all 0.2s;
      }
    }
  }
}

/********************
      Footer
********************/

.footer-section-nav {
  overflow: hidden;
  .footer-item {
    margin-top: 15px;
    padding-left: 15px;
    border-right: 1px solid @border-color-base;

    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      li {
        padding: 2px 0;
        color: #5c5c5c;
        a {
          color: #5c5c5c;
          transition: all 0.3s;
          &:hover,
          &:focus {
            text-decoration: none;
            color: @green;
            border-color: rgba(174, 174, 174, 0.7);
          }
        }
      }
    }
  }

  .footer-item__title {
    padding-bottom: 8px;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 500;

    &.social {
      padding-top: 25px;
    }
  }

  @media (min-width: 992px) {
    .footer-item__title {
      font-size: 14px;
    }
  }
}

.footer {
  padding: 15px 0;
  border-top: 1px solid @border-color-base;
  background-color: #f7f7f7;

  .navy-line {
    width: 180px;
    height: 1px;
    margin: 40px auto 0;
    border-bottom: 1px solid @border-color-base;
  }

  .footer-copyright {
    padding-top: 15px;
    color: #919191;
    font-size: 13px;

    p {
      padding-bottom: 0;
      margin-bottom: 0;
    }

    a {
      color: #919191;
    }
  }
}

.footer-item__cell-social {
  height: 67px;
  a {
    padding: 5px;
    color: #828282;
    font-size: 24px;
    transition: all 0.3s;
    &:hover {
      color: @text-color-base;
    }
  }
}

/****************************
        Регистрация
****************************/

.register-form,
.signin {
  width: 360px;
  margin: 0 auto;
  padding-bottom: 15px;

  h1 {
    font-size: 18px;
    font-weight: 400;
  }

  p.small {
    padding-bottom: 15px;
  }

  // Что бы иконки соцю сетей поместились в одну строку
  form,
  .divider {
    margin-left: 30px;
    margin-right: 30px;
  }
}

.register-concord-text {
  font-style: italic;
  font-family: "PT Sans",sans-serif;
  font-size: 13px;

  a {
    color: @text-color;
    font-weight: 500;
    transition: all 0.3s;
    &:hover,
    &:focus {
      color: @green;
      text-decoration: @link-hover-decoration;
    }
  }
}

/****************************
        Главная
****************************/

.feature {
  margin-top: 20px;
  margin-bottom: 20px;
  text-align: center;

  .feature-icon {
    vertical-align: middle;
    height: 90px;
    margin: 0 auto 15px;
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: contain;
  }

  .money-green {
    width: 62px;
    background-image: url(../img/money_green.svg);
  }

  .no-intermediaries-green {
    width: 96px;
    background-image: url(../img/no-intermediaries_green.svg);
  }

  .large-base-green {
    width: 64px;
    background-image: url(../img/large-base_green.svg);
  }

  .free_green {
    width: 64px;
    background-image: url(../img/free_green.svg);
  }
}


/****************************
        sidebar
****************************/

.sidebar {
  margin-bottom: 15px;
}

#yandex_ads {
  padding-bottom: 15px;
}

.sticky {
  position: fixed;
}
.stop {
  position: relative;
}

.side-search {
  padding: 15px;
  border: 1px solid @border-color-base;
  background-color: #f7f7f7;

  .bootstrap-select:not([class*="col-"]):not([class*="form-control"]):not(.input-group-btn) {
    width: 100%;
  }

