.sidebar-title {
  padding-bottom: 15px;
  font-size: 18px;
  font-weight: 500;
  color: @black-base;
}

.sidebar-group {
  padding-bottom: 15px;
}

#districtModal,
#metroModal {
  .checkbox {
    display: block;
  }
}

.metro-btn,
.district-btn {
  span {
    font-size: (@font-size-base - 2);
  }
  a {
    display: inline-block;
    margin-right: -4px;
    padding: 0 0 0 3px;
    color: #999;
    line-height: 1;
  }
}

#districtModalBody {
  margin-top: -10px;
}

.modal-district {
  min-width: @modal-md;
}

@media (min-width: @screen-md) {
  .modal-district {
    width: @modal-lg;
  }
}

.filter_modal_title {
  padding: 10px 0 4px;
  color: #898989;
  font-weight: 500;
}

.modal-metro {
  width: 770px;

  .modal-body {
