    font-size: 12px;
    color: #3c3c3c;
    border-bottom: 1px solid rgba(174, 174, 174, 0.3);
    transition: 0.3s;
    &:hover,
    &:focus {
      text-decoration: none;
      color: @green;
      border-color: rgba(174, 174, 174, 0.6);
    }
  }
}

/*******************************
    Стили списка объвлений
*******************************/

@grid-list-border-color:                @border-color-base;;
@grid-list-paddin-vertical:             15px;

.search-controls {
  position: relative;
  padding: 0 0 10px;
  .clearfix();

  .bootstrap-select {
    margin-top: -26px;

  }

  select {
    margin-top: -26px;
  }
}

.realty-list-container {
  margin-bottom: 15px;
}

.item_colorized {
  background: #fff4d4;
}

.realty-list {
  display: block;
  padding: 15px 0;
  border-top: 1px solid @grid-list-border-color;
  color: inherit;
  .clearfix();

  &:hover {
    color: inherit;

    &:not(.account) {
      .r-l-type {
        color: @green;
      }
    }
  }

  .r-l-img {
    position: relative;
    display: block;
    box-shadow: rgba(0, 17, 51, 0.35) 0 1px 3px 0;
    img {
      display: inline-block;
      min-height: 128px;
      max-width: 100%;
      height: auto;
    }

    .count-icon {
      position: absolute;
      right: 12px;
      bottom: 6px;
      padding: 4px 10px;
      display: inline-block;
      font-weight: 500;
      color: #fff;
      vertical-align: middle;
      border-radius: @border-radius-small;
      background: rgba(0, 0, 0, 0.4) none repeat scroll 0 0;
    }
  }

  &.account {
    .preview {
      float: right;
    }
  }
}

.realty-list-content {
  position: relative;
  .r-l-type {
    padding: 0;
    font-size: 18px;
    font-weight: 500;
    @media (max-width: @screen-md) {
      font-size: 18px;
    }
  }

  .r-l-address {
    font-size: (@font-size-base + 1);
  }

  .data_add {
    display: block;
    padding: 6px 0 5px;
    color: @grey-base;
    font-size: (@font-size-base - 1);
    span {
      padding-left: 15px;
      color: #000;
    }
  }

  .r-l-description {
    height: 38px;
    margin-bottom: 4px;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #525557;
    font-size: (@font-size-base - 1);

    @media (min-width: @screen-lg-min) {
      min-height: 60px;
    }
  }

  .r-l-bottom {
    color: #525557;
    .r-l-floor {
      float: left;
      padding-right: 10px;
      font-size: (@font-size-base - 2);
      span {
        font-weight: bold;
      }
    }
  }

  .r-l-price {
    position: absolute;
    top: 2px;
    right: -15px;
    padding-left: 40px;
    background-color: #fff;
    background: linear-gradient(90deg, rgba(255,255,255,0) 0, rgba(255,255,255,1) 34px);
    color: @text-color-base;
    font-size: 18px;
    font-weight: 500;
    line-height: 1.1;
    i {
      font-size: 18px;
    }

    span {
      font-size: 12px;
      font-weight: 300;
    }
  }
}


h1.title {
  margin: 0 0 24px;
  font-size: 18px;
  line-height: 28px;
  text-transform: uppercase;
}

