}
.v-dotted-line__left {
  width: 60%;
  margin-right: -60%;
  overflow: hidden;
}
.v-dotted-line__title {
  display: inline;
}
.v-dotted-line__line {
  display: inline-block;
  width: 100%;
  margin: 0px -100% 0px 0px;
  vertical-align: baseline;
  border-bottom: 1px dotted @border-color-base;
}
.v-dotted-line__right {
  margin-left: 60%;
}
.v-dotted-line__content {
