}

.similar-block {
  margin-top: 30px;
}

.similar-item {
  height: 340px;
  margin-bottom: 15px;
  margin-right: 1px;
  border: 1px solid #e7eaec;
  transition: all .2s linear;
  color: @text-color-base;
  &:hover {
    box-shadow: 0 1px 5px 1px rgba(0,0,0,.1);
  }
}

.similar-img {
  position: relative;
  height: 190px;
  margin: -1px -1px 0;
  background-color: #f8f8f9;
  overflow: hidden;
  img {
    width: 100.5%;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
  }

  .count-icon {
    position: absolute;
    top: 10px;
    left: 10px;
    padding: 4px 10px;
    display: inline-block;
    font-weight: 500;
    color: #fff;
    vertical-align: middle;
    border-radius: 2px;
    background: rgba(0, 0, 0, 0.4) none repeat scroll 0 0;
  }
}

.similar-content {
  position: relative;
  padding: 10px;
  height: calc(100% - 190px);
  p {
    max-height: 64px;
    overflow: hidden;
    &.title {
      margin-bottom: 1.6em;
      font-weight: normal;
      font-style: italic;
      line-height: 1.333em;
      font-family: "PT Serif",serif;

    }
  }
}

@media (min-width: 1200px) {

  .similar-block__item__img {
    height: 185px;
  }

  .similar-block__item__content {
    height: calc(100% - 185px);
