/*******************************
           dropzone
*******************************/
#addDropzone.dropzone {
  padding: 15px;
  border: 2px dashed @border-color-base;
  border-radius: 2px;

  .dz-message {
    color: #c4c4c4;

    .fa {
      font-size: 28px;
    }
  }

  .dz-preview {
    margin: 5px;
    padding: 4px;
    border: 1px solid @border-color-base;
    box-shadow: 0 1px 1px 0 rgba(34, 36, 38, 0.07);
    .dz-image {
      border-radius: 0;
    }

    .dz-progress {
      height: 6px;
      margin-top: -12px;
      width: 110px;
      margin-left: -55px;
      background: #fff;
      border-radius: 3px;
      .dz-upload {
        background: @green;
      }
    }

    .dz-error-message {
      left: -6px;
    }

    .dz-remove {
      margin-top: 5px;
      &:hover {
        text-decoration: none;
      }
    }

    .dz-error-mark,
    .dz-success-mark {
      margin-top: -36px;
    }
  }
}

/*******************************
           messages
*******************************/

.messages {
  background: #FAFAFB;
  border: 1px solid #e7eaec;
  border-left-width: 3px;
  margin: 0 0 20px;
  padding: 15px;
  border-radius: 2px;

  &:hover,
  &:focus {
    background: #fff;
  }

  .message--title {
    margin-top: 0;
    margin-bottom: 5px;
    font-size: 18px;
    font-weight: 500;
  }

  &.messages--error {
    border-left-color: #a51b00;
    .message--title {
      color: #a51b00;
    }
  }

  &.messages--warning {
    border-left-color: #f8ac59;
    .message--title {
      color: #f8ac59;
    }
  }

  &.messages--success {
    border-left-color: @green;
    .message--title {
      color: @green;
    }
  }

  &.messages--info {
    border-left-color: #1c84c6;
    .message--title {
      color: #1c84c6;
    }
  }

  &.messages--danger {
    border-left-color: #ed5565;
    .message--title {
      color: #ed5565;
    }
  }

}
