/*******************************
           Блог
*******************************/

.blog-row {
  margin-right: -15px;
}

.blog-preview-block {
  clear: both;
  height: 430px;
  margin-bottom: 15px;
  margin-top: 0;
  margin-left: -15px;
  padding: 0;
  border: 1px solid #e7eaec;
  transition: all .2s linear;
  color: @text-color-base;
  &:hover {
    box-shadow: 0 1px 5px 1px rgba(0,0,0,.1);
  }
}

.blog-preview-img {
  height: 200px;
  background-color: #f8f8f9;
  overflow: hidden;
  img {
    height: 100%;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
  }
}

.blog-preview-content {
  position: relative;
  padding: 20px;
  height: calc(100% - 200px);
  p {
    max-height: 64px;
    overflow: hidden;
    &.title {
      color: #262626;
      font-size: 18px;
      line-height: 22px;
      margin-bottom: 6px;
      font-weight: 500;
    }
  }

  .blog-preview-bottom {
    position: absolute;
    bottom: 15px;
    width: calc(100% - 30px);
    height: 20px;

  }
}

.wrapper-blog {
  margin-bottom: 15px;
  .title {
    margin: 0 0 10px;
    font-size: 22px;
    text-transform: inherit;
  }

  h2 {
    font-size: 18px !important;
  }

  .article-date {
    margin-bottom: 15px;
  }
}
